export function iconAnimations(){

    let icons = $(".lottie-icon");

    icons.each((i, item) => {
        let src = item.getAttribute("data-json");

        let temp = bodymovin.loadAnimation({
            container: item,
            path: src,
            autoplay: false,
            renderer: "svg", //'svg/canvas/html'
            name: `icon${i}`
        });

        temp.setSpeed(1);

        $(item).parent(".icon-parent").hover(
            //mouseenter
            function(){
                temp.setDirection(1);
                temp.play();
            },

            //mouseout
            function(){
                temp.setDirection(-1);
                temp.play();
            }
        );
    });


}


