export function initSingleMap() {
	
	let map;
	const style = [
		{
			"featureType": "administrative",
			"elementType": "labels.text.fill",
			"stylers": [
				{
					"color": "#444444"
				}
			]
		},
		{
			"featureType": "landscape",
			"elementType": "all",
			"stylers": [
				{
					"color": "#f2f2f2"
				}
			]
		},
		{
			"featureType": "poi",
			"elementType": "all",
			"stylers": [
				{
					"visibility": "off"
				}
			]
		},
		{
			"featureType": "poi.business",
			"elementType": "geometry.fill",
			"stylers": [
				{
					"visibility": "on"
				}
			]
		},
		{
			"featureType": "road",
			"elementType": "all",
			"stylers": [
				{
					"saturation": "-100"
				},
				{
					"lightness": "0"
				}
			]
		},
		{
			"featureType": "road.highway",
			"elementType": "all",
			"stylers": [
				{
					"visibility": "simplified"
				}
			]
		},
		{
			"featureType": "road.arterial",
			"elementType": "labels.icon",
			"stylers": [
				{
					"visibility": "off"
				}
			]
		},
		{
			"featureType": "transit",
			"elementType": "all",
			"stylers": [
				{
					"visibility": "off"
				}
			]
		},
		{
			"featureType": "water",
			"elementType": "all",
			"stylers": [
				{
					"color": "#91a4b3"
				},
				{
					"visibility": "on"
				}
			]
		}
	];
	const googleMap = $('.google-map');
	
	const coords = {
		lat: Number(googleMap.attr('data-lat')),
		lng: Number(googleMap.attr('data-lng')),
	};
	
	const logo = googleMap.attr('data-logo');
	const title = googleMap.attr('data-title');
	
	const zoom = Number(googleMap.attr('data-zoom'));
	
	function initMap() {
		map = new google.maps.Map(document.querySelector('.google-map'), {
			center: coords,
			zoom: zoom,
			styles: style,
			icon: false,
			disableDefaultUI: true
		});
		
		let marker = new google.maps.Marker({
			position: coords,
			map: map
		});
		
		marker.setVisible(false);
		
		let infoWindow = new google.maps.InfoWindow({
			content: `<div class="info-window">
						<div class="icon">
							<img src=${logo} alt="icon">
						</div>
						<div class="title">${title}</div>
					</div>`,
		});
		
		infoWindow.open(map, marker);
	}
	
	initMap();
}


